/* Icon */

	.icon {
		@include icon;
		border-bottom: none;
		position: relative;
		text-align: center;

		> .label {
			display: none;
		}

		&.style2 {
				border-radius: 2.75em;
				display: inline-block;
				height: 2.75em;
				line-height: 2.75em;
				width: 2.75em;
		}

		&.major {
			display: block;
			margin: 0 0 (_size(element-margin) * 0.5) 0;

			&:before {
				font-size: 1.25rem;
			}
		}

		&.mega {
			display: block;
			margin: 0 0 (_size(element-margin) * 0.5) 0;

			&:before {
				font-size: 3.25rem;
			}
		}
	}

	a.icon {
		&.style2 {
			z-index: 1;
			color: _palette(fg-bold);
			&:after {
				opacity:0;
				top: -2px;
				left: -2px;
				padding: 2px;
				z-index: -1;
				pointer-events: none;
    		position: absolute;
				content: '';
				border-radius: 50%;
				box-sizing: content-box;
				width: 100%;
				height: 100%;
				@include vendor('transform','scale(1.3)');
				@include vendor('transition',(
					'opacity #{_duration(transition)}',
					'transform #{_duration(transition)}'
				));
			}
		}
	}

	@mixin color-icon($p: null) {

 		.icon {
 			&.style2 {
				box-shadow: 0 0 0 _size(border-width) _palette($p, border);
				color: _palette($p, fg-bold);
				@include vendor('transition','color #{_duration(transition)}');
				&:after {
					background: _palette($p, color1);
				}
 			}
 		}

		a.icon {
			&.style2 {
				&:hover {
					color: _palette(invert,fg-bold);
					&:after {
						@include vendor('transform','scale(1)');
						opacity: 1;
					}
				}

			/*	&:active {
					&:before {
						background-color: transparentize(_palette($p, accent), 0.9);
						box-shadow: inset 0 0 0 _size(border-width) _palette($p, accent);
						color: _palette($p, accent);
					}
				} */
			}
		}
	}

	@include color-icon;
