// Misc.
	$misc: (
		z-index-base:		10000,
		overlay-opacity:	0.5,
		lightbox-opacity:	0.75,
		gallery-limit:		32,
		items-limit:		16
	);

// Duration.
	$duration: (
		menu:				0.5s,
		transition:			0.2s,
		gallery-lightbox:	0.5s,
		gallery-delay:		0.15s,
		items-delay:		0.15s,
		on-load:			0.75s,
		on-scroll:			0.75s
	);

// Size.
	$size: (
		border-radius:		4px,
		border-radius-alt:	0.5rem,
		border-width:		1px,
		element-height:		2.75rem,
		element-margin:		2rem,
		gutter:				3.5rem,
		inner:				64rem,
		padding:			(
			default:		7rem,
			xlarge:			5rem,
			large:			4rem,
			medium:			3rem,
			small:			2rem
		)
	);

// Font.
	$font: (
		family-display:	 ('Roboto Slab', serif),
		family: 				('Roboto', sans-serif),
		family-fixed:		('Courier New', monospace),
		weight:				300,
		weight-bold:		400,
		kerning:			-0.05em,
		kerning-alt:		0.125em
	);

// Palette.
	$palette: (
		color1:				#0438CC,
		color2:				#506399,
		color3:				#1FDDFF,
		color4:				#FF965F,
		color5:				#FF4B1F,
		color6:				#64abb4,
		color7:				#6ba78c,

		bg:					#ffffff,
		bg-alt:				#eeeeee,
		fg:					#000000,
		fg-bold:			#000000,
		fg-light:			rgba(0,0,0,0.75),
		border:				rgba(0,0,0,0.2),
		border-alt:			rgba(0,0,0,0.075),
		border-bg:			rgba(0,0,0,0.05),
		accent:				#0438CC,

		invert: (
			bg:				#000000,
			bg-alt:			#222222,
			fg:				#ffffff,
			fg-bold:		#ffffff,
			fg-light:		#ffffff,
			border:			rgba(255,255,255,1.0),
			border-alt:		rgba(255,255,255,0.125),
			border-bg:		rgba(255,255,255,0.125),
			accent:			#0438CC
		),
	);

	//easing functions
	$ease: (
  in-quad:      cubic-bezier(0.550,  0.085, 0.680, 0.530),
  in-cubic:     cubic-bezier(0.550,  0.055, 0.675, 0.190),
  in-quart:     cubic-bezier(0.895,  0.030, 0.685, 0.220),
  in-quint:     cubic-bezier(0.755,  0.050, 0.855, 0.060),
  in-sine:      cubic-bezier(0.470,  0.000, 0.745, 0.715),
  in-expo:      cubic-bezier(0.950,  0.050, 0.795, 0.035),
  in-circ:      cubic-bezier(0.600,  0.040, 0.980, 0.335),
  in-back:      cubic-bezier(0.600, -0.280, 0.735, 0.045),
  out-quad:     cubic-bezier(0.250,  0.460, 0.450, 0.940),
  out-cubic:    cubic-bezier(0.215,  0.610, 0.355, 1.000),
  out-quart:    cubic-bezier(0.165,  0.840, 0.440, 1.000),
  out-quint:    cubic-bezier(0.230,  1.000, 0.320, 1.000),
  out-sine:     cubic-bezier(0.390,  0.575, 0.565, 1.000),
  out-expo:     cubic-bezier(0.190,  1.000, 0.220, 1.000),
  out-circ:     cubic-bezier(0.075,  0.820, 0.165, 1.000),
  out-back:     cubic-bezier(0.175,  0.885, 0.320, 1.275),
  in-out-quad:  cubic-bezier(0.455,  0.030, 0.515, 0.955),
  in-out-cubic: cubic-bezier(0.645,  0.045, 0.355, 1.000),
  in-out-quart: cubic-bezier(0.770,  0.000, 0.175, 1.000),
  in-out-quint: cubic-bezier(0.860,  0.000, 0.070, 1.000),
  in-out-sine:  cubic-bezier(0.445,  0.050, 0.550, 0.950),
  in-out-expo:  cubic-bezier(1.000,  0.000, 0.000, 1.000),
  in-out-circ:  cubic-bezier(0.785,  0.135, 0.150, 0.860),
  in-out-back:  cubic-bezier(0.680, -0.550, 0.265, 1.550),
	plastic: cubic-bezier(0.77, 0, 0.175, 1)
);
