/* Banner (transitions) */

	.banner {

		// Mixin.
			@mixin transition-banner($event) {
				$x: null;
				$y: null;

				@if ($event == 'load') {
					$x: 'body.is-loading &';
					$y: _duration(on-load);
				}
				@else if ($event == 'scroll') {
					$x: '&.is-inactive';
					$y: _duration(on-scroll);
				}

				// Content.
					&.on#{$event}-content-fade-up {
						.content {
							@include vendor('transition', (
								'opacity #{$y} ease-in-out',
								'transform #{$y} ease-in-out'
							));
						}

						#{$x} {
							.content {
								@include vendor('transform', 'translateY(1rem)');
								opacity: 0;
							}
						}
					}

					&.on#{$event}-content-fade-down {
						.content {
							@include vendor('transition', (
								'opacity #{$y} ease-in-out',
								'transform #{$y} ease-in-out'
							));
						}

						#{$x} {
							.content {
								@include vendor('transform', 'translateY(-1rem)');
								opacity: 0;
							}
						}
					}

					&.on#{$event}-content-fade-left {
						.content {
							@include vendor('transition', (
								'opacity #{$y} ease-in-out',
								'transform #{$y} ease-in-out'
							));
						}

						#{$x} {
							.content {
								@include vendor('transform', 'translateX(1rem)');
								opacity: 0;
							}
						}
					}

					&.on#{$event}-content-fade-right {
						.content {
							@include vendor('transition', (
								'opacity #{$y} ease-in-out',
								'transform #{$y} ease-in-out'
							));
						}

						#{$x} {
							.content {
								@include vendor('transform', 'translateX(-1rem)');
								opacity: 0;
							}
						}
					}

					&.on#{$event}-content-fade-in {
						.content {
							@include vendor('transition', 'opacity #{$y} ease-in-out');

							.icons li {
								@include unroll;

							}
						}

						#{$x} {
							.content {
								opacity: 0;

								.icons li {
										opacity: 0;
								}
							}
						}
					}

				// Image.
					&.on#{$event}-image-fade-up {
						.image {
							@include vendor('transition', (
								'opacity #{$y} ease-in-out',
								'transform #{$y} ease-in-out'
							));

							img {
								@include vendor('transition', 'opacity #{$y} ease-in-out');
								@include vendor('transition-delay', '#{$y * 0.75}');
							}
						}

						#{$x} {
							.image {
								@include vendor('transform', 'translateY(1rem)');
								opacity: 0;

								img {
									opacity: 0;
								}
							}
						}
					}

					&.on#{$event}-image-fade-down {
						.image {
							@include vendor('transition', (
								'opacity #{$y} ease-in-out',
								'transform #{$y} ease-in-out'
							));

							img {
								@include vendor('transition', 'opacity #{$y} ease-in-out');
								@include vendor('transition-delay', '#{$y * 0.75}');
							}
						}

						#{$x} {
							.image {
								@include vendor('transform', 'translateY(-1rem)');
								opacity: 0;

								img {
									opacity: 0;
								}
							}
						}
					}

					&.on#{$event}-image-fade-left {
						.image {
							@include vendor('transition', (
								'opacity #{$y} ease-in-out',
								'transform #{$y} ease-in-out'
							));

							img {
								@include vendor('transition', 'opacity #{$y} ease-in-out');
								@include vendor('transition-delay', '#{$y * 0.75}');
							}
						}

						#{$x} {
							.image {
								@include vendor('transform', 'translateX(1rem)');
								opacity: 0;

								img {
									opacity: 0;

								}
							}
						}
					}

					&.on#{$event}-image-fade-right {
						.image {
							@include vendor('transition', (
								'opacity #{$y} ease-in-out',
								'transform #{$y} ease-in-out'
							));

							img {
								@include vendor('transition', 'opacity #{$y} ease-in-out');
								@include vendor('transition-delay', '#{$y * 0.75}');
							}

						}

						#{$x} {
							.image {
								@include vendor('transform', 'translateX(-1rem)');
								opacity: 0;

								img {
									opacity: 0;
										}
							}
						}
					}

					&.on#{$event}-image-fade-in {
						.image {
							img, {
								@include vendor('transition', 'opacity #{$y} cubic-bezier(0.785, 0.135, 0.15, 0.86)');
							}

							&::before, {
								@include vendor('transition', 'opacity #{$y} cubic-bezier(0.785, 0.135, 0.15, 0.86)');
								@include vendor('transition-delay', '#{$y * 0.15}');
							}
							&::after, {
								@include vendor('transition', 'opacity #{$y} cubic-bezier(0.785, 0.135, 0.15, 0.86)');
								@include vendor('transition-delay', '#{$y * 0.15}');
							}
						}

						#{$x} {
							.image {
								img {
									opacity: 0;
								}
								&::before {
									opacity: 0;
								}
								&::after {
									opacity: 0;
								}
							}
						}
					}

					&.on#{$event}-image-slide-left {
						.image {
							@include vendor('transition', (
								'opacity #{$y} cubic-bezier(0.785, 0.135, 0.15, 0.86)',
								'transform #{$y} cubic-bezier(0.785, 0.135, 0.15, 0.86) #{$y * 0.75}'
							));

							img {
								@include vendor('transition', 'opacity #{$y} ease-in-out');
								@include vendor('transition-delay', '#{$y * 0.75}');
							}
						}

						#{$x} {
							.image {
								@include vendor('transform', 'translateX(77rem)');
								opacity: 1;

								img {
									opacity: 1;
								}
							}
						}
					}

					&.on#{$event}-image-slide-down {
						.image {
							@include vendor('transition', (
								'opacity #{$y} cubic-bezier(0.785, 0.135, 0.15, 0.86)',
								'transform #{$y} cubic-bezier(0.785, 0.135, 0.15, 0.86) #{$y * 0.75}'
							));

							img {
								@include vendor('transition', 'opacity #{$y} ease-in-out');
								@include vendor('transition-delay', '#{$y * 0.75}');
							}
						}

						#{$x} {
							.image {
								@include vendor('transform', 'translateY(-77rem)');
								opacity: 1;

								img {
									opacity: 1;
								}
							}
						}
					}

					&.on#{$event}-image-color-fade {
						.image {
							&::before {
								@include vendor('transition', 'opacity #{$y} cubic-bezier(0.785, 0.135, 0.15, 0.86)');
							}
							&::after {
								@include vendor('transition', 'opacity #{$y} cubic-bezier(0.785, 0.135, 0.15, 0.86)');
							}
						}

						#{$x} {
							.image {
								&::before {
									opacity: 0;
								}
								&::after {
									opacity: 0;
								}
							}
						}
					}

			}

		// On Load.
			@include transition-banner('load');

		// On Scroll.
			@include transition-banner('scroll');

	}

