				border-radius: 0;
			}
		}

		@include breakpoint(xlarge) {
			@include padding(_size(padding, xlarge), _size(padding, xlarge));
		}

		@include breakpoint(large) {
			@include padding(_size(padding, large), _size(padding, large));
		}

		@include breakpoint(medium) {
			@include padding(_size(padding, medium) * 1.25, _size(padding, medium));
		}

		@include breakpoint(small) {
			@include padding(_size(padding, small) * 1.25, _size(padding, small));
		}

		// Modifiers.

			// Size.
				&.fullscreen {
					min-height: 100vh;
				}

			// Content Alignment.
				&.content-align-left {
					text-align: left;
				}

				&.content-align-center {
					// ...
				}

				&.content-align-right {
					text-align: right;
				}

			// Image Position.
				&.image-position-left {
					.image {
						img {
							@include vendor('object-position', 'left');
						}
					}
				}

				&.image-position-center {
					// ...
				}

				&.image-position-bottom {
					.image {
						img {
							@include vendor('object-position', 'bottom');
						}
					}				}

				&.image-position-right {
					.image {
						img {
							@include vendor('object-position', 'right');
						}
					}
				}

	}

	/* Banner (style6) */

		.banner.style6 {
			$image-size: 24rem;
			$content-size: (_size(inner) * 0.875) - $image-size - (_size(element-margin) * 1.75);

			@include padding(_size(padding, default), _size(padding, default));
			@include vendor('align-items', 'center');
			@include vendor('display', 'flex');
			@include vendor('flex-direction', 'row-reverse');
			@include vendor('justify-content', 'center');
			position: relative;
			text-align: left;
			overflow-x: hidden;

			.content {
				width: $content-size;
				max-width: 100%;
			}

			.image {
				@include vendor('flex-grow', '0');
				@include vendor('flex-shrink', '0');
				width: $image-size;
				height: $image-size;
				border-radius: 100%;
				margin: 0 (_size(element-margin) * 1.75) _size(element-margin) 0;
				background-color: transparent;

				&::before {
					-webkit-clip-path: polygon(0 0, 80% 0%, 100% 20%, 100% 100%, 80% 100%, 20% 100%, 0% 80%, 0% 20%);
clip-path: polygon(0 0, 80% 0%, 100% 20%, 100% 100%, 80% 100%, 20% 100%, 0% 80%, 0% 20%);
				}

				&::after {
					-webkit-clip-path: polygon(0 0, 80% 0%, 100% 20%, 100% 100%, 80% 100%, 20% 100%, 0% 80%, 0% 20%);
clip-path: polygon(0 0, 80% 0%, 100% 20%, 100% 100%, 80% 100%, 20% 100%, 0% 80%, 0% 20%);
				}


				img {
					@include vendor('object-fit', 'cover');
					@include vendor('object-position', 'center');
					display: block;
					width: 100%;
					height: 100%;
					-webkit-clip-path: polygon(0 0, 80% 0%, 100% 20%, 100% 100%, 80% 100%, 20% 100%, 0% 80%, 0% 20%);
					clip-path: polygon(0 0, 80% 0%, 100% 20%, 100% 100%, 80% 100%, 20% 100%, 0% 80%, 0% 20%);
				}
			}

			@include breakpoint(xlarge) {
				@include padding(_size(padding, xlarge), _size(padding, xlarge));
			}

			@include breakpoint(large) {
				@include padding(_size(padding, large), _size(padding, large));
			}

			@include breakpoint(medium) {
				@include padding(_size(padding, medium) * 1.25, _size(padding, medium));

				.image {
					width: ($image-size * 0.875);
					height: ($image-size * 0.875);
				}
			}

			@include breakpoint(small) {
				@include padding(_size(padding, small) * 1.25, _size(padding, small));
				@include vendor('align-items', 'flex-start');

				.image {
					width: ($image-size * 0.75);
					height: ($image-size * 0.75);
					margin: 0 (_size(element-margin) * 1) _size(element-margin) 0;
				}
			}

			@include orientation(portrait) {
				@include vendor('align-items', 'center');
				@include vendor('flex-direction', 'column-reverse');
				text-align: center;

				.content {
					width: 34rem;
					max-width: 100%;
				}

				.image {
					margin-right: 0;
				}
			}

			// Modifiers.

				// Size.
					&.fullscreen {
						min-height: 100vh;
					}

				// Orientation.
					&.orient-left {
						@include vendor('flex-direction', 'row');

						.image {
							margin: 0 0 _size(element-margin) (_size(element-margin) * 1.75);
						}

						@include breakpoint(small) {
							.image {
								margin: 0 0 _size(element-margin) (_size(element-margin) * 1);
							}
						}

						@include orientation(portrait) {
							@include vendor('flex-direction', 'column-reverse');

							.image {
								margin-left: 0;
							}
						}
					}

					&.orient-right {
						// ...
					}

				// Content Alignment.
					&.content-align-left {
						// ...
					}

					&.content-align-center {
						text-align: center;
					}

					&.content-align-right {
						text-align: right;
					}

				// Image Position.
					&.image-position-left {
						.image {
							img {
								@include vendor('object-position', 'left');
							}
						}
					}

					&.image-position-center {
						// ...
					}

					&.image-position-right {
						.image {
							img {
								@include vendor('object-position', 'right');
							}
						}
					}

		}


// Mixin

	@mixin color-banner($p: null) {
		.banner {
			.image {
				background-color: transparentize(_palette($p, fg-bold), 1);
			}

			@if ($p != 'invert') {
				&.invert {
					.image {
						background-color: transparentize(_palette(invert, fg-bold), 0.875);
					}
				}
			}
		}

		.banner.style4 {
			.image {
				@include color-phone($p);
			}

			// Variant.
				&.iphone {
					.image {
						@include color-phone-variant('iphone', $p);
					}
				}

				&.android {
					.image {
						@include color-phone-variant('android', $p);
					}
				}

			@if ($p != 'invert') {
				&.invert {
					.image {
						@include color-phone(invert);
					}

					// Variant.
						&.iphone {
							.image {
								@include color-phone-variant('iphone', invert);
							}
						}

						&.android {
							.image {
								@include color-phone-variant('android', invert);
							}
						}

				}
			}
		}
	}

	@include color-banner;
